@-webkit-keyframes fade-in-out {
    0%   {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fade-in-out {
    0%   {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes fade-in {
    from {
      -webkit-transform: translateY(10vh);
              transform: translateY(10vh);
      opacity: 0;
    }
    to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
}

@keyframes fade-in {
    from {
      -webkit-transform: translateY(10vh);
              transform: translateY(10vh);
      opacity: 0;
    }
    to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
}

.text-background {
    background-color: rgba(255,255,255,0.5);
    -webkit-box-shadow: 0 0 50px 20px rgba(255,255,255,0.5);
            box-shadow: 0 0 50px 20px rgba(255,255,255,0.5);
}

.welcome,
.intro {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.welcome {
    -webkit-animation: fade-in-out 2s both;
            animation: fade-in-out 2s both;
}

.intro {
    -webkit-animation: fade-in 1s 2s both;
            animation: fade-in 1s 2s both;
}

.intro button {
    -webkit-animation: fade-in 1s 3s both;
            animation: fade-in 1s 3s both;
}


